import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutations'
import actions from './action'
import getters from './getters'

Vue.use(Vuex)

const state = {
	userInfo: {},//
	userId: null,//ID
	token: null,//
	itemList: {},//
	editingItem: {//
		item_sync_key: null,
		item_name: null,
		item_emergency_level: 5,
		item_importance_level: 5,
		category_id: '0',
	},
	categoryPickerList: [],//Picker
	categoryMap: {},//
}

export default new Vuex.Store({
	state,
	getters,
	actions,
	mutations,
})


